      <h1 class="title">计算机科学与技术学院</h1>
      <div class="mainText">
        <div class="mainTextDiv">大连理工大学计算机专业创建于1974年，并于1981年发展成计算机科学与工程系。2009年，成立计算机科学与技术学院。目前，大连理工大学计算机科学与技术学院拥有1个博士后流动站、2个博士点和3个硕士点，其中，计算机应用技术硕士点建于1986年，博士点建于2000年，2002年建成辽宁省重点学科；计算机软件与理论硕士点建于1992年，博士点建于2002年；计算机系统结构硕士点建于2002年。2008年大连理工大学计算机科学与技术建成辽宁省一级重点学科，2009年建成计算机科学与技术博士后流动站。学院由计算机技术研究所、计算机软件与理论研究所、计算机应用技术研究所、计算机系统结构研究所组成。

</div>
        <div class="mainTextDiv">大连理工大学计算机科学与技术学科现有中国科学院院士（兼）1人，有博士生导师16人，教授19人，副教授22人，具有博士学位的教师41名，具有较强的科研实力。近年来，计算机科学与技术学院取得了可喜成绩，先后获得国家科学技术进步二等奖1项、辽宁省科技进步1等奖1项、省部级二等奖5项、三等奖7项。近三年先后在国内外重要学术期刊、国际学术会议发表论文604篇，其中，被SCI索引146篇，EI索引256篇, ISTP索引72篇。

</div>
        <div class="mainTextDiv">根据《中国大学及学科专业评级报告2008-2009》全国一级学科排名，大连理工大学计算机科学与技术一级学科位于全国前9.25%，在全国227所高校中位列21位，在大连理工大学42个一级学科位列第10位。

</div>
<div class="mainTextDiv">本学科培养出了数以千计的计算机专业人才，毕业生遍布国内外，桃李满天下。近几年共培养博士学位研究生和学术硕士研究生近900人，专业学位硕士生 1000余人，本科毕业生近3000人，为国家计算机专业人才的培养做出重要贡献。</div>
<div class="mainTextDiv">目前，全院教师齐心协力、汇聚智慧，开拓进取，建立“引才、育才、聚才、用才”机制，以《国家中长期科学和技术发展规划纲要(2006━2020年》为指导，制定了“三步走”发展战略，准确定位、明确了发展思路、确定了发展模式，建立了 “十二五”期间计算机科学与技术学院发展的具体目标，重点发展领域和优先发展的研究方向。围绕中国《创新2050：科技革命与中国的未来》提出的八大经济社会基础和战略体系，我们正在着力培育学科发展的增长点、抢抓机遇，迎接第三次信息产业革命的挑战，为实现学院各方面工作的全面、和谐、可持续和跨越式发展而奋斗。</div>
