      <h1 class="title">信息与通信工程学院</h1>
      <div class="mainText">
        <div class="mainTextDiv">大连理工大学信息与通信工程学院的前身系原大连工学院电讯系，始建于1949年，由我国著名的电子通信工程专家毕德显院士担任首任系主任；1981年扩建为电子工程系和计算机科学与工程系；1997年电子工程系、计算机科学与工程系和自动化系三系组建成电子与信息工程学院；2009年，大连理工大学实行学部制，以电子工程系为基础组建了信息与通信工程学院，隶属于新成立的电子信息与电气工程学部。</div>
        <div class="mainTextDiv">学院以培养信息与通信学科的高层次人才为根本任务，在不断优化师资结构的同时，努力提高学生培养质量和学科建设水平。学院现有教师32人，其中教授12人。学院具有信息与通信工程一级学科博士学位授予权，设有信息与通信工程博士后流动站以及信号与信息处理和通信与信息系统2个博士点，是辽宁省一级重点学科，拥有数字媒体处理与传输省级重点实验室。电子信息工程本科专业为国家特色专业。</div>
        <div class="mainTextDiv">学院不仅是人才培养基地，也是信息科学与通信技术的研究基地。近五年来，学院教师在国内外重要的学术期刊和学术会议上发表论文逾500篇，出版学术专著和教材20余部，并承担了“973计划”、“863计划”、国家科技攻关、国防预研和国家自然科学基金等国家级科研项目40余项，在随机信号处理理论、语音信号处理、成像与图像处理、多媒体信息安全和通信系统与网络等领域取得了丰硕的科研成果，并有多项成果获得了国家及省部级奖励。此外，学院已完成和正在承担来自地方政府部门和企事业单位的课题百余项，获得了显著的经济效益和社会效益，推动了地方经济的发展。</div>
        <div class="mainTextDiv">信息科学和通信技术已成为21世纪我国的重要发展战略目标。学院师生将紧紧抓住国家振兴东北地区等老工业基地的历史性机遇和学校“985工程”、“211工程”建设的发展契机，发扬大连理工大学“团结、求实、进取、创新”的优良传统，努力将学院建成我国信息与通信领域的科学研究和高层次人才培养基地。</div>
