        <div class="mainTextDiv">学院设有“控制理论与控制工程”、 “检测技术与自动化装置”、“系统工程”和“模式识别与智能系统” 四个硕士点，“控制理论与控制工程”博士点，“控制科学与工程”博士后流动站，“控制理论与控制工程”辽宁省重点学科， “工业装备先进控制技术” 辽宁省重点实验室、 “工业装备监测与控制” 教育部工程研究中心和辽宁省高等学校“控制理论与控制工程”创新团队。本学科的研究领域一直是国际、国内最活跃、发展速度最快的重要领域之一。近年来，本学科以流程工业建模与集成优化控制、复杂工业过程综合自动化、工业以太网现场总线控制系统、模糊控制、智能机器人为主要研究方向，在国内外核心期刊发表论文300余篇，完成国家“863计划”重点课题、国家“十五”科技攻关项目、国家自然科学项目、教育部骨干教师基金、辽宁省重大科技攻关项目等30余项。本学科成功承办“2001年中国控制会议”、“2003年IFAC（国际自动控制联合会）冶金自动化国际学术会议”和“2006年全球智能控制与自动化大会”，使本学科在国内外有了很大的影响，其发展速度之快正在为国内外同类专业的学校所关注。</div>
