        <div class="mainTextDiv">1949年建校初期时成立电讯工程系和电机工程系，著名电子通信工程专家毕德显院士任两系系主任。1952年全国高校调整，电讯工程系调到解放军通信工程学院，电机工程系调到东北工学院。1956年，成立机电系并以全国著名控制理论学者王众托院士为首成立了工业企业电气化专业,1960年重建无线电工程系，1980年无线电工程系更名为电子工程系，1981年成立计算机科学与工程系，1994年成立自动化系。1997年电子工程系、自动化系和计算机科学与工程系三系组建成电子与信息工程学院，2007年成立生物医学工程系。学校于1991年创办电磁工程系，2000年更名为电气工程与应用电子技术系。2009年12月12日电子与信息工程学院、电气工程与应用电子技术系整合组建电子信息与电气工程学部。</div>
