      <h1 class="title">电气工程学院简介</h1>
      <div class="mainText">
        <div class="mainTextDiv">电气工程学院的前身是创办于1991年的电磁工程系，2000年更名为电气工程与应用电子技术系，2009年成立电气工程学院，隶属于电子信息与电气工程学部。学院设有“电力电子研究所”、“电力系统研究所”、“静电与特种电源研究所”、“电气传动与电能变换研究所”、“臭氧技术与设备研究所”、“电工教研室”、 “电气工程中心实验室”和“电气学院创新实践基地”。</div>
        <div class="mainTextDiv"> 学院设有“电气工程”一级学科博士点，拥有电机与电器、电力系统及其自动化、电力电子与电力传动、高电压与绝缘技术和电工理论与新技术二级学科博士和硕士授予权，其中“电机与电器”二级学科为辽宁省重点学科；拥有电气工程专业学位授予权；1个本科专业（电气工程及其自动化）为国家特色专业建设点。现有教职工50人，其中教授(研究员)22人，副教授(高级工程师)20人；博士生导师19人；在校本科生508人，硕士生185人，博士生40人。</div>
        <div class="mainTextDiv">近年来，我院教师获得宝钢教育基金优秀奖、辽宁省青年骨干教师、大连理工大学教学名师等多项荣誉称号。“电工学”课程为首批辽宁省精品课程，“电工教学团队”为辽宁省教学团队。师资队伍中，有中国电工技术学会电弧电接触专委会副主任委员、中国电工技术学会电磁发射专委会副主任委员、中国物理学会静电专业委员会副主任委员、中国电力教育协会电力学科教学委员会委员、教育部高等学校电气工程及自动化专业教学指导委员会委员、教育部本科工程教育专业认证电子信息与电气工程类专业认证专家组成员、中国机械工业教育协会电气工程与自动化学科委员会副主任、中国电源学会常务理事、全国高等学校电路信号系统教学与教材研究会常务理事、中国电机工程学会理论电工专业委员会委员等，并担任《电力系统及其自动化学报》、《电工技术学报》、《高压电器》、《高电压技术》等核心期刊的编委。</div>
        <div class="mainTextDiv">教师科研项目丰富，近年来，学院教师承担了包括国家自然科学基金面上项目和重点项目、国家863计划、国家支撑计划、国家重点科技攻关课题、GF项目以及地方政府部门和企事业单位等横向课题，多项成果获省部级奖励，部分成果达到国际领先水平，年人均经费超过30万元，在国内外重要学术刊物上年均发表研究论文100余篇。研究成果在国内外具有较高的学术地位，并与英国利物浦大学、美国普渡大学、日本东京工业大学、加拿大滑铁卢大学等国外高校有密切的合作关系。</div>
        <div class="mainTextDiv">学院始终坚持“以人才培养为核心，以学科建设促发展”的理念，注重人才培养质量，发挥学科建设的龙头作用，凝练方向、突出特色、汇聚队伍，创新人才培养模式和科学研究体系，不断推进全院快速发展。</div>
