        <div class="mainTextDiv">生物医学工程学院隶属于大连理工大学电子信息与电气工程学部，以培养生物医学工程领域的高层次人才为根本任务，是东北地区该领域人才培养和科学研究的主要基地。</div>
        <div class="mainTextDiv">目前，该学院所有教师都拥有博士学位，具有留学海外名校研究背景的教师占90%以上。近年来，在“211工程”和“985工程”支持下，已建成一支高素质和国际化的的教学与科研队伍，并拥有生物医学工程一级学科的博士和硕士学位授予权、设有生物医学工程博士后流动站、是辽宁省一级重点学科，已经建成完整的本-硕-博人才培养体系。该学院不断加强教学和科研两个基地的建设，拥有本科教学“生物医学工程”实验室、“医学信息技术与康复工程”与“985工程”科研平台，并配置多台国内外一流的仪器设备，为高水平的教学、工程实践和科研工作提供了强有力的保障与支撑。</div>
        <div class="mainTextDiv">近年来，该学院教师承担多项国家级科研项目，在国内外主流学术刊物上发表论文逾200篇，出版学术专著和教材10多部，并获得多项省部级科学技术奖励。在生物医学信息检测与处理、生物医学成像与图像处理、系统生物学、远程医疗、仿生机器人与康复工程、神经工程和光电生物信息检测等领域取得了丰硕的教学与科研成果。此外，生物医学工程学院与大连医科大学、大连中山医院及多个地方政府部门和企事业单位建有密切的科研合作关系，已完成和正在承担多项来自合作单位的研究课题，获得了显著的经济和社会效益。同时，该学院非常重视教育与科研的国际化工作，与美国、英国、法国、德国、芬兰、日本和韩国等国家的多所知名大学进行联合培养本科与研究生，并开展国际科研合作。</div>
