//该文件专门用于创建应用的路由器
import { createRouter,createWebHashHistory } from "vue-router";
import home from "../view/home.vue";
import survey from "../view/survey.vue";
import research from "../view/research.vue"
import teacher from "../view/teacher.vue";
import undergrad from "../view/undergrad.vue";
import graduateStudent from "../view/graduateStudent";
import employ from "../view/employ.vue"
import rules from "../view/rules.vue"
import download from "../view/download.vue"
import surveyHome from "../view/surveyChildren/surveyHome.vue"
import surveyLeader from "../view/surveyChildren/surveyLeader.vue"
import surveyMajor from "../view/surveyChildren/surveyMajor.vue"
import surveyOptional from "../view/surveyChildren/surveyOptional.vue"
import ai from "../view/surveyChildren/ai.vue";
import automation from "../view/surveyChildren/automation.vue"
import biomedicine from "../view/surveyChildren/biomedicine.vue"
import electric from "../view/surveyChildren/electric.vue"
import telemessage from "../view/surveyChildren/telemessage.vue"
import computer from "../view/surveyChildren/computer.vue"
import work from "../view/teacherChildren/workFirst.vue"
import office from "../view/teacherChildren/officeFirst.vue"
import award from "../view/researchChildren/award.vue"
import notice from "../view/researchChildren/notice.vue"
import partent from "../view/researchChildren/partent.vue"
import researchHome from "../view/researchChildren/researchHome.vue"



const routes = [
        {
            path:'/',
            redirect:'/home'
        },
        {
            path:'/home',
            component:home
        },
        {
            path:'/survey',
            component:survey,
            redirect:'/survey/surveyHome',
            children:[
            // {
            //     path:'',
            //     redirect:'surveyHome'
            // },
            {   
                path:'surveyHome',
                component:surveyHome,
            },
            {
                path:'surveyLeader',
                component:surveyLeader
            },
            {
                path:'surveyMajor',
                component:surveyMajor
            },
            {
                path:'surveyOptional',
                component:surveyOptional
            },
            {
                path:'ai',
                component:ai
            },
            {
                path:"automation",
                component:automation
            },
            {
                path:"biomedicine",
                component:biomedicine
            },
            {
                path:'electric',
                component:electric
            },
            {
                path:'computer',
                component:computer
            },
            {
                path:'telemessage',
                component:telemessage
            },
            ]
        },
        {
            path:'/research',
            component:research,
            redirect:'/research/researchHome',
            children:[
                {
                    path:'researchHome',
                    component:researchHome
                },
                {
                    path:'award',
                    component:award
                },
                {
                    path:'notice',
                    component:notice
                },
                {
                    path:'partent',
                    component:partent
                }
            ]
        },
        // {
        //     path:'/lab',
        //     component:lab
        // },
        {
            path:'/teacher',
            component:teacher,
            redirect:'/teacher/workFirst',
            children:[
                {   
                    path:"workFirst",
                    component:work
                },
                {
                    path:'officeFirst',
                    component:office
                }
            ]
        },
        // {
        //     path:'/party',
        //     component:party
        // },
        {
            path:'/undergrad',
            component:undergrad
        },
        {
            path:'/graduateStudent',
            component:graduateStudent
        },
        {
            path:'/employ',
            component:employ
        },
        {
            path:'/rules',
            component:rules
        },
        {
            path:'/download',
            component:download
        },
        
    ];

const router = createRouter({
    history:createWebHashHistory(),
    routes
});


export default router;